import styled from "styled-components";
import styles from "./Stack.module.css";
import { getStackIconFromStorage } from "../../../../storage/imageStorage";

const Container = styled.div`
  color: #e7e7e7;
  width: 10rem;
  padding: 1.5rem 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 800px) {
    width: 5rem;
  }
`;

const Icon = styled.div`
  background-image: url(${(props) =>
    getStackIconFromStorage(props.stack.name) ||
    getStackIconFromStorage("questionMark")});
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 4rem;
  height: 4rem;

  @media (max-width: 800px) {
    width: 2rem;
    height: 2rem;
  }
`;

const Title = styled.span`
  margin-top: 0.5rem;
  font-size: 1.3rem;

  @media (max-width: 800px) {
    font-size: 1.1rem;
  }
`;

const Stack = ({ item }) => {
  return (
    <Container item={item}>
      <Icon className={`${styles.icon}`} stack={item}></Icon>
      <Title>{item.name}</Title>
    </Container>
  );
};

export default Stack;
