import styled from "styled-components";
import { MyBarData } from "./MyBarData";
import MyBar from "./MyBar";
const Container = styled.div`
  width: 80%;
  min-height: 90%;
  max-height: 90%;
  max-width: 55rem;
  min-width: 33rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  overflow: hidden;
`;

const Content = styled.iframe`
  position: relative;
  width: 710px;
  height: 1100px;
  left: 50%;
  transform: translateX(-50%);
  border: none;
  padding: 0;
  margin: 0;
`;

const Resume = () => {
  return (
    <Container>
      <Content
        loading="lazy"
        src="https://www.canva.com/design/DAFQbWDxHWc/view?embed"
        allowfullscreen="allowfullscreen"
        allow="fullscreen"
      ></Content>
    </Container>
  );
};

export default Resume;
