import "../../../fonts/font.css";
import styled from "styled-components";
import TechStacks from "./TechStacks/TechStacks";
import { useSelector } from "react-redux";
import Message from "./Message/Message";
import Resume from "./Resume/Resume";

const Container = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 3rem;
  font-family: "BMJUA";
`;

const MenuWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  @keyframes rollout {
    0% {
      opacity: 0;
      transform: translateX(-1rem);
    }
    50% {
    }
    100% {
      opacity: 1;
      transform: none;
    }
  }

  & {
    animation: rollout 1s;
  }
`;

const Title = styled.h2`
  color: #e7e7e7;
`;

const Desktop = () => {
  const menuStack = useSelector((state) => state);
  const getLastMenu = () => menuStack[menuStack.length - 1];
  return (
    <Container>
      {getLastMenu() === "message" && (
        <MenuWrapper>
          <Message />
        </MenuWrapper>
      )}
      {getLastMenu() === "techStack" && (
        <MenuWrapper>
          <TechStacks />
        </MenuWrapper>
      )}
      {getLastMenu() === "resume" && (
        <MenuWrapper>
          <Resume />
        </MenuWrapper>
      )}
      {getLastMenu() === "others" && (
        <MenuWrapper>
          <Title>Something Awesome Is Comming </Title>
        </MenuWrapper>
      )}
    </Container>
  );
};

export default Desktop;
