import styled from "styled-components";
import Stack from "./Stack";
import styles from "./TechStacks.module.css";
import { techStackData } from "../../../../storage/dataStorage";
import { Fragment } from "react";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem;
  overflow: auto;
`;

const StackWrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 1.5rem 0;
`;

const Seperator = styled.div`
  width: 50%;
`;

const TechStacks = () => {
  const techStackTypes = [...new Set(techStackData.map((stack) => stack.type))];
  return (
    <Container className={`${styles.scroll}`}>
      {techStackTypes.map((type, idx) => (
        <Fragment key={type}>
          {idx !== 0 && <Seperator className={`${styles.seperator}`} />}
          <StackWrapper>
            {techStackData
              .filter((stack) => stack.type === type)
              .map((item, idx) => (
                <Stack key={idx} item={item} />
              ))}
          </StackWrapper>
        </Fragment>
      ))}
    </Container>
  );
};

export default TechStacks;
