import styled from "styled-components";
import { tasks } from "../../../storage/dataStorage";
import { getTaskIconFromStorage } from "../../../storage/imageStorage";
import { useSelector, useDispatch } from "react-redux";
import { changeMenu } from "../../../store";

const Container = styled.div`
  background-color: #00000033;
  position: absolute;
  z-index: 99999;
  height: 3rem;
  display: flex;
  flex-direction:row;
  align-items: center;
  justify-content: center;
  bottom: 0;
  left: 0;
  right: 0;
}
`;

const Icon = styled.div`
  background-image: url(${(props) =>
    getTaskIconFromStorage(props.task.name) ||
    getTaskIconFromStorage("questionMark")});
  background-size: cover;
  margin: 0 1rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;

  & {
    transition: 0.5s;
    transform: scale(1);
    z-index: -1;
  }
  &:hover {
    box-shadow: 0 0 1.2rem #faa1a1b3;
    border-radius: 50%;
    transition: 0.5s ease-out;
    transform: scale(1.2);
    z-index: -1;
  }
`;

const Taskbar = () => {
  const menuStack = useSelector((state) => state);
  const dispatch = useDispatch();
  const getLastMenu = () => menuStack[menuStack.length - 1];

  const onIconClick = ({ name }) => {
    if (name === getLastMenu()) {
      return;
    }
    dispatch(changeMenu(name));
  };

  return (
    <Container>
      {tasks.map((item) => (
        <Icon key={item.name} onClick={() => onIconClick(item)} task={item} />
      ))}
    </Container>
  );
};

export default Taskbar;
